//changing the colors of the carousel indicators
div.carousel-indicators > [data-bs-target] {
    background-color: black;
}

//changing the colors of the carousel controls - next & previous
button.carousel-control-next > span.carousel-control-next-icon, button.carousel-control-prev > span.carousel-control-prev-icon {
    background-color: black;
    width: 4rem;
    height: 4rem;
}

//reduce the width of the container around the previous and next icon buttons for ease of link access
.carousel-control-width {
    width: 5%;
}

//Center the header
div.top-experiences-header {
    width: fit-content;
    margin: 0 auto;
}


div.top-experiences-header > h1 {
    width: max-content;
    font-size: 2.5rem;
    font-weight: 700;
    line-height: 1.26;
    margin-top: 16px;
    margin-bottom: 16px;
    color: white;
}

div.top-experiences-header > p {
    font-size: 20px;
    line-height: 1.7;
    max-width: 754px;
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding-bottom: 4rem;
    color: white;

    @media (max-width: 700px) {
        display:none;
        
    }
}

.top-experiences-carousel {
    padding: 0 69px;
    grid-gap: 4.9375rem;
    gap: 4.9375rem;
}
.homepage-top-experiences {
    padding-bottom: 4rem;
    padding-top: 4rem;
    background: linear-gradient(306deg,#2e2e2e 102%,#444343 0);
}

.top-experiences-card-header {

   // width: fit-content;
    //margin: 0 auto;
    background-color: white;
    border-bottom: 0;
}

.top-experiences-card-header > h2{
    display: flex;
    align-items: center;
    margin-bottom: 0;
    padding: 2.875rem 4.1875rem;
    font-size: 27px;
    font-weight: 700;
}

.card-custom {
    //min-height: 819px;
    min-height: 600px;
    background: #fff;
    color: #313131;
    display: grid;
}

.articles {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
}

.article {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.article img {
    width: 292px;
    aspect-ratio: 1/1;
}

.article.first {
    position: relative;
    justify-content: end;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.article.first img {
    width: 460px;
    height: 100%;
    margin: 0;
}

.article.second {
    flex-direction: column-reverse;
    justify-content: flex-end;
    padding-bottom: 2.5rem;
}

.articleContent {
    padding: 2.5rem 2rem;
    width: 360px;
    background: #fff;
    border-radius: 16px;
}

.article.first .articleContent{
    position: absolute;
    bottom: 7%;
    left: 50%;
    height: 360px;
    transform: translateX(-50%);
}

.articleContent h4 {
    color: #9b8856;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 700;
    line-height: 2.15;
    letter-spacing: 2.5px;
    margin-bottom: .75rem;
}

.articleContent h3 {
    font-size: 24px;
    font-weight: 700;
    line-height: 1.21;
    margin-bottom: .5rem;
}

.articleContent p {
    font-size: 16px;
    line-height: 1.63;
    margin-bottom: 1rem;
}

.articleContent .ctaButton {
    font-size: 14px;
    color: #818181;
    background-color: #e7e7e7!important;
    padding: .5rem 1rem;
    min-height: unset;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border-radius: 1rem;
}

.button.primary {
    background-color: #009688;
}

.button:hover {
    text-decoration: none;
}

.ctaButton:hover {
    background-color: #dcdcdc!important;
}


.button {
    display: inline-block;
    text-decoration: none;
    min-height: 3rem;
    line-height: 1.3;
    color: #fff;
    font-size: 1rem;
    font-weight: 700;
    padding: .8125rem 1.5rem .875rem;
    border-radius: 1rem;
    border: none;
    text-align: center;
    -webkit-appearance: none;
    text-transform: capitalize;
}







