/*Banner Styling using LANDKIT*/
//Check out the image styling before deploying - since the VH does not render correctly on jekyll
.pi-banner-image {
    height: 300px;
    @media (min-width:768px) {
        height: 450px;
    }
    @media (min-width:1024px) {
        height: 550px;
    }
}

.pi-header-badge{
    width: 50px;
    height: 100%;
    @media (min-width:768px) {
        width: 70px;
        height: 70px;
    }
    @media (min-width:1024px) {
        width: 120px;
        height: 120px;
    }
}

.pi-start-10 {
    left: 5%;
    @media (min-width: 768px) {
        left: 10%;
    }
    @media (min-width: 1024px) {
        left: 15%;
    }
}

.pi-bottom-20 {
    bottom: 20%;
    @media (min-width: 1024px) {
        bottom: 35%;
    }
}

.pi-header-text-block {
    justify-content: center;
}

.pi-header-text-h2 {
    font-size: 2rem;
    line-height: 2.4rem;
    @media (min-width: 768px) {
        font-size: 2.625rem;
        line-height: 3.15rem;     
    }
    @media (min-width: 1024px) {
        font-size: 6.1875rem;
        line-height: 7.425rem;
        letter-spacing: .05em;
    }
}

.pi-header-subtitle-text {
    font-size: 1.125rem;
    line-height: 1.35rem;
    font-weight: 400;
    @media (min-width: 768px) {
        font-size: 1.25rem;
        line-height: 1.5rem;
    }
    @media (min-width: 1024px) {
        font-size: 2rem;
        line-height: 2.4rem;
    }
}

