//OVERRIDING PROPERTIES

//global overrides
.nav-link {
    color: #454442;
}

//particularly the mobile version below

.pi-mobile-subnav {
    @media (max-width:768px) {
    display: none;        
    }
}

//this was acting weird - dropdown items were over the nav bar and offset. also had a scrollbar
.navbar-collapse-fix{
    position: relative;
    overflow-y:hidden;
    left: 0;
    box-shadow: none;
}

//Simply set the color and shadow but above needs to be removed on collapse
.pi-subnav {
    box-shadow: 0 0.125rem 0.5625rem 0 hsl(0deg 0% 80% / 50%);
    background-color: white;
}

//remove the general border by default on navbar toggler
.pi-navbar-toggler {
    border: 0;
    border-radius: 0;
}

//Remove that ugly black border when toggler icon clicked
.pi-navbar-toggler:focus{
    box-shadow: none;
}

//override the default active state coloring and style on particularly the mobile version
.pi-mobile-subnav .navbar-collapse .navbar-nav .nav-item.pi-nav-item .pi-nav-link.active {
    background: #e4f2f2;
    border-left: 6px solid #239493;
}

//remove the padding that ruins the active state
.navbar-collapse .navbar-nav .nav-item.pi-nav-item {
    padding: 0;
}

.navbar-collapse .navbar-nav .nav-item.pi-nav-item .pi-nav-link {
    padding: 1rem;
}


//DESKTOP SUBNAV
.pi-desktop-subnav {
    box-shadow: 0 0.125rem 0.5625rem 0 hsl(0deg 0% 80% / 50%);
    border-radius: 12px;
}


.pi-desktop-subnav .container-fluid .navbar-nav{
    flex-grow: 1;
    //justify-content: center;
    justify-content: space-around;
}
.pi-desktop-subnav .container-fluid .navbar-nav li {
    display: flex;
    height: 4.625rem;
}

.pi-desktop-subnav .container-fluid .navbar-nav li a {
    display: flex;
    align-items: center; 
    justify-content: center; 
    height: 100%;      
}

.pi-desktop-subnav .container-fluid .navbar-nav li a.active {
    box-shadow: inset 0 -0.3125rem #394451;
}

.pi-desktop-subnav{
    padding-top: 0;
    padding-bottom: 0;
    @media (max-width: 768px) {
        display: none;
    }
}
