// Devices
$medium-screen: 1080px;
$tablet-screen: 768px;
$mobile-screen: 480px;


// G2A Type Systems
$lato: "Lato", sans-serif;


// Font Size
$typo_lion: 2.6rem;
$typo_panther: 1.8rem;
$typo_leopard: 1.3rem; 
$typo_cat: 1rem; 
$typo_kitten: .8rem;

//DEFAULT JEKYLL
// Primary Colors
$white: #ffffff;
$black: #222222;
$pink: hotpink;
// Accent Colors
$blue: lightskyblue;
$green: lightseagreen;


//GO2AFRICA
// G2A Theme Colors
//Primary Colors
$primary-dark-grey: #454442;
$primary-kepple: #00b5a4;
$primary-lion: #9c8f6c;
$primary-orange: #ed5723;

//Secondary Colors
$grey-80: #7d7b7a;
$grey-60: #b4b4b5;
$grey-40: #dcd9d9;
$grey-20: #ececeb;
$white: #ffffff;

// Base Fonts
$base-font-size: 16px;
$base-line-height: 1.2;
$base-font-color: $primary-dark-grey;

// larger standout fonts
$larger-standout-font-size: 22px; 
$h2-font-size: 20px;

// Headline Fonts
$headline-line-height: 1.3;
$headline-font-color: darken($base-font-color, 10%);

// Padding
$large-pad: 1.5rem;
$base-pad: 1rem;
$small-pad: $large-pad / 2; //0.75rem