.why-travel-background {
    background-image: url(/src/assets/images/home/why-travel/background-with-plane.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

img.why-travel-icon {
    margin-left: -0.6rem;
}

.header-wrapper{
    font-size: 2rem;
    margin-bottom: 4.1875rem;
}

.card-margin {
    @media (min-width: 576px ) {
        margin-bottom: 5.625rem;    
    }
    
}

.card-description {
    line-height: 1.625;
}

//conditional render on larger than mobile
.card-grid {
    @media (max-width: 576px){
        display: none;
    }
}

//conditional render on mobile devices
.why-travel-carousel {
    @media (max-width: 576px){
        display: block;
    }
    @media (min-width: 576px){
        display: none;
    }
}

.carousel-card{
    @media (max-width: 320px){
        padding:1rem 0.8rem;
    }
    max-width: 22rem;
    margin-left:auto;
    margin-right: auto;
    padding:2.625rem 2.125rem;
    border-radius: .75rem;
    box-shadow: 0 2px 17px 3px hsla(0,0%,73.3%,.6);
}

.carousel-space {
    padding: 1rem;
    margin-bottom: 1.5rem;
}

//Something must be done about this
.carousel-control-color {
    color:#9b8856;
    background-color: #9b8856;
    border-radius: 2rem;
    width: 1.7rem;
    height: 1.7rem;
}


//STATS
.stat {
    text-align: center;
}
.stat-value {
    color: #9b8856;
    font-size: 6.25rem;
    font-family: $lato;
    @media (max-width: 760px){
        font-size: 3.125rem
    }
}

.stat-description {
    font-size: 1.25rem;
    line-height: 1.34;
    // max-width: 15.875rem;
}

//CTA BUTTON STYLES
.cta-button-wrapper {
    padding: 1.5625rem;
}

.cta-button {
    border-radius: 1.5625rem;
    color: #141414;
    background-color: #fff;
    border: 1px solid #141414;
    font-weight: 400;
}

.cta-button:hover {
    text-decoration: none;
}
.button-big {
    padding: 0 1.5rem;
    min-height: 3.125rem;
    line-height: 3.125rem;
}

