.ctaButton {
    font-size: 14px;
    color: #818181;
    background-color: #e7e7e7 !important;
    padding: .5rem 1rem;
    min-height: unset;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border-radius: 1rem;
}


.button {
    display: inline-block;
    text-decoration: none;
    min-height: 3rem;
    line-height: 1.3;
    color: #fff;
    font-size: 1rem;
    font-weight: 700;
    padding: .8125rem 1.5rem .875rem;
    border-radius: .125rem;
    border: none;
    text-align: center;
    text-transform: capitalize;
    cursor: pointer;
}

.top-destination-card {
    color: #9b8856;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 700;
    line-height: 2.15;
    letter-spacing: 2.5px;
    margin-bottom: .75rem;
}

.top-destinations-card .articleContent {
    padding: 2.5rem 2rem;
    width: 360px;
    background: #fff;
}